
.ask-name {
  position: fixed;
  color: #3B0085;
  font-size: .5rem;
  background: #F6DF00;
  /*padding: .1rem;*/
  border: .1rem solid #3B0085;
  border-radius: .2rem;
  text-align: center;
  /*font-weight: bold;*/
  bottom: 5.4rem;
  left: 1.5rem;
  z-index: 10;
}
.bottom-img {
  position: fixed;
  left: 0;
  bottom: 0;
}
.talk {
  width: 100%;
  background: #C9D0EA;
  z-index: 8;
  /*max-height: 100%;*/
  /*overflow-y: scroll;*/
}
#bottom {
  width: 100%;
  height: 6.5rem;
  background: #C9D0EA;
}
.input-box input {
  position: fixed;
  left: 0.2rem;
  bottom: 0.2rem;
  width: 7rem;
  height: 1rem;
  border: .05rem solid #ccc;
  border-radius: .1rem;
  padding: 0 .2rem;
}
.input-box button {
  position: fixed;
  right: 0.2rem;
  bottom: 0.2rem;
  width: 2rem;
  height: 1rem;
  border: .05rem solid #059406;
  border-radius: .1rem;
  background: #0BB20C;
  color: #fff;
  text-align: center;
}
.input-box button:disabled {
  opacity: .5;
}
.talk .row {
  width: 9.6rem;
  float: left;
  padding: .2rem;
  /*background: #f00;*/
}
.row .tip {
  color: #fff;
  font-size: .3rem;
  text-align: center;
}
.row .tip span {
  border-radius: .1rem;
  background: rgba(153, 153, 153, .5);
  padding: .1rem;
}
.row .avatar {
  width: 1rem;
  height: 1rem;
  /*padding: .1rem;*/
  /*background: #0f0;*/
}
.avatar img {
  width: 100%;
  height: 100%;
  border-radius: .2rem;
}
.row .img-box {
  position: relative;
  width: 3rem;
  height: 2.56rem;
  border-radius: .2rem;
  float: left;
  margin-left: .5rem;
}
.img-box img {
  border-radius: .2rem;
}
.row .box {
  position: relative;
  max-width: 5rem;
  border: .08rem solid #999;
  border-radius: .2rem;
  padding: .3rem;
  font-size: .5rem;
}
.report {
  position: relative;
  max-width: 5rem;
  float: left;
  margin-left: .5rem;
  padding: 0;
}
.report img {
  height: 5.4rem;
  border-radius: .2rem;
  border: .08rem solid rgb(33, 55, 127);
}
.other .avatar {
  float: left;
  border: .08rem solid #3B0085;
  border-radius: .2rem;
  /*background: url(../assets/other.jpg);
  background-size: 1rem;*/
}
.other .box {
  float: left;
  margin-left: .5rem;
  background: #fff;
}
.me .box {
  float: right;
  margin-right: .5rem;
  background: #fff;
}
.other .box em {
  position: absolute;
  left: -.8rem;
  top: .2rem;
  display: block;
  border-color: transparent #999 transparent transparent;
  border-width: .4rem;
  border-style: solid;
}
.report em {
  border-color: transparent rgb(33, 55, 127) transparent transparent !important;
}
.now .box {
  border: .08rem solid orange;
}
.now em {
  border-color: transparent orange transparent transparent !important;
}
.other .box span {
  position: absolute;
  left: -.65rem;
  top: .24rem;
  display: block;
  border-color: transparent #fff transparent transparent;
  border-width: .36rem;
  border-style: solid;
}
.me .avatar {
  float: right;
  margin-top: 1rem;
  border: .08rem solid orange;
  border-radius: .2rem;
  /*background: url(../assets/me.jpg);
  background-size: 1rem;*/
}
.my-text .avatar {
  margin-top: 0;
}
.my-text .box {
  border: .08rem solid orange;
}
.my-text .box em {
  position: absolute;
  right: -.8rem;
  top: .2rem;
  display: block;
  border-color: transparent transparent transparent orange;
  border-width: .4rem;
  border-style: solid;
}
.my-text .box span {
  position: absolute;
  right: -.65rem;
  top: .24rem;
  display: block;
  border-color: transparent transparent transparent #fff;
  border-width: .36rem;
  border-style: solid;
}
.me .response {
  float: right;
  display: block;
  width: 4.5rem;
}
.bottom-btn {
  position: fixed;
  bottom: .25rem;
  width: 4rem;
}
.no-btn {
  left: .8rem;
}
.yes-btn {
  right: .8rem;
}
.answer-btn-enter-active {
  -webkit-animation: btn-scale 2s;
          animation: btn-scale 2s;
}
@-webkit-keyframes btn-scale {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
}
10% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
70% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
85% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes btn-scale {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
}
10% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
70% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
85% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}



