
.page {
  width: 100%;
  height: 100%;
  font-size: 1rem;
  background-color: rgb(88, 95, 184);
  position: relative;
}
.full-width-img {
  width: 100%;
}
.face-container {
  position: fixed;
  left: 24.3%;
  /*top: 26%;;*/
  top: 4.6rem;
  width: 2.3rem;
}
.face {
  width: 100%;
}
.page1 h1 {
  color: #fff;
  font-size: .5rem;
  text-align: center;
  font-weight: normal;
}
.bottom {
  width: 100%;
  margin-top: .2rem;
}
.page .btn {
  width: 60%;
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}
.page2 {
  padding: .2em .5rem;
  position: relative;
}
.title_bg {
  margin-top: 1.4rem;
}
.top-bar {
  width: 100%;
  padding: .2rem 0;
}
.top-bar img {
  display: block;
  width: 90%;
  margin: 0 auto;
}
.container {
  width: 85%;
  height: 55%;
  position: fixed;
  top: 4rem;
  padding: .2rem;
}
.container span {
  font-size: .5rem;
}
.container .head {
  width: 100%;
  margin-bottom: .1rem;
}
.head .title {
  width: 75%;
  color: rgb(33, 55, 127);
  font-size: .7rem;
  float: left;
}
.head .change {
  width: 25%;
  color: rgba(33, 55, 127, 0);
  font-size: .6rem;  
  float: left;
}
.clearfix {
  clear: both;
}
.container .answer {
  background: #ffd766;
  padding: .2rem;
  font-size: .5rem;
  color: rgb(33, 55, 127);
  margin-bottom: .3rem;
  border-radius: .2rem;
}
.container .not-check::before {
  content: '';
  background: url(../assets/check.png) left bottom no-repeat;
  background-size: 100% auto;
  width: .8rem;
  height: .8rem;
  display: inline-block;
  margin-right: .1rem;
}
.container .check::before {
  content: '';
  background: url(../assets/check.png) left top no-repeat;
  background-size: 100% auto;
  width: .8rem;
  height: .8rem;
  display: inline-block;
  margin-right: .1rem;
}
.btn-container {
  width: 100%;
  position: absolute;
  left: 0;
  top: 7.2rem;
}
.next-btn {
  width: 45%;
  margin: 0 auto;
  display: block;
}
.real-next-enter-active {
  -webkit-animation: scale-in .8s;
          animation: scale-in .8s;
}
.real-next-leave-active {
  -webkit-animation: all .5s;
          animation: all .5s;
  opacity: 0;
}
@-webkit-keyframes scale-in {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes scale-in {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@-webkit-keyframes fade-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes fade-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
.container-bottom {
  color: #b46e53;
  font-size: .8rem;
  position: absolute;
  top: 9rem;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}



